@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
ul {
    margin: auto;
}

li {
    color: black;
    list-style: none;
}


.other-list-container {
    text-align: center;
    padding-bottom: 4%;
}

.other-list-item {
    flex: 1 1;
    flex-grow: 1;
}
.rank-list {
    border: solid 1px black;
    /* margin: 2%; */
}

.create-button {
    color: blue;
    margin-bottom: 1%;
}

.rank-list-col {
    display: flex;
    justify-content: space-between;
}

.rank-list {
    flex: 1 1;
    max-width: 23%;
}
.banner-img {
    /* width: 100%; */
    height: auto;
    opacity: .1;
    /* text-align: center; */
}

.banner {
    margin-top: 2%;
    height: 100%;
    overflow: hidden;
    text-align: center;
}

.center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: 'Abril Fatface', cursive;
}

.other-list {
    display: flex;
}
body {
    font-family: 'Roboto', sans-serif;
}
